<template>	
            <!-- <div style="height:100%;width:100%">
				<div v-for="i in list">
					<el-row   :gutter="12" type="flex" justify="center">
					  <el-col style="background-color:aqua" :span="15">
							{{ msg }} <router-link to="/index"></router-link>
						</el-col>
					</el-row>
					<br>
				</div>
            </div> -->
	<div class="text-warpper">
		<div class="text-content">
			<div class="box-card" v-for="(item,index) in text" :key="index">
				<div class="header">
					<span>{{item.title}}</span>
					<el-button style="padding: 3px 0;margin-right:7px;" type="text" @click="toDetail"></el-button>
				</div>
				<div class="content">
					<p>{{item.detail}}</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:"index3",
		data(){
			return {
				text:[
					{title:',',detail:'iii'},
					{title:'',detail:''},
					{title:'',detail:''},
					{title:'',detail:''},
					{title:'',detail:''},
				],
			msg:"122.3."
			}
			
		},
		methods:{
			toDetail(){
				this.$router.push('/index')
			}
		}
	}
</script>
<style lang="less" scoped>
	.text-warpper{
		min-height: 300px;
		width:80%;
		padding:0;
		margin:50px auto;
		background: #F0EDE3;
		.text-content{
			// width:1126px;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			flex-wrap: wrap;
			margin:0 auto;
			
			.box-card {
				width: 330px;
				height: 250px;
				margin-right: 40px;
				margin-bottom: 40px;
				box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
				border: 1px solid #EBEEF5;
				border-radius: 5px;
				background-color: #fff;
				color: #303133;
				&:nth-of-type(3n){
					margin-right: 0;
				}
				.header{
					height: 50px;
					line-height: 50px;
					display: flex;
					justify-content: space-between;
					align-items: center;
					border-bottom: 1px solid #eee;
					padding:0 15px;
					span{
						font-size: 14px;
						width:250px;
						text-align: left;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
				}
				.content{
					font-size: 14px;
					text-align: left;
					padding:10px;
					height: 173px;
					p{
						text-indent: 30px;
						line-height: 24px;
						height:100%;
						overflow: hidden;
					}
				}
			}

		}
		
	}
</style>>
 



